a {
  display: table;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
  *outline: 0;
}

.btn {
  position: relative;
  background-color: transparent;
  border: none;
  border-radius: 0.875rem;
  font-weight: bold;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
  *outline: 0;
  overflow: hidden;
}
.btn-lg {
  font-size: 1.25rem;
  line-height: 1.5rem;
  padding: 1.25rem 1.875rem;
}
@media screen and (max-width: 539px) {
  .btn-lg {
    font-size: 1.125rem;
    line-height: 1.375rem;
  }
}
.btn-sm {
  font-size: 1rem;
  line-height: 1.25rem;
  padding: 0.9375rem 1.25rem;
}
@media screen and (max-width: 531px) {
  .btn-sm {
    padding: 0.625rem 1.25rem;
  }
}
@media screen and (max-width: 531px) {
  .btn {
    border-radius: 0.625rem;
  }
}
.btn__yellow {
  border: 0.125rem solid #F4BC49;
  border-radius: 0.875rem;
  background-color: #F4BC49;
  box-shadow: 0px 15px 15px -10px rgba(244, 188, 73, 0.4);
  color: #312F2E;
}
@media screen and (min-width: 1200px) {
  .btn__yellow {
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
  }
  .btn__yellow:hover {
    background-color: #f4b049;
  }
}
.btn__with-arrow {
  color: #312F2E;
  transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
  padding: 0.875rem 0;
}
.btn__with-arrow .arrow-wrapper {
  position: relative;
  display: inline-block;
  margin-left: 0.375rem;
  height: 0.75rem;
  width: 2.125rem;
  overflow: hidden;
}
.btn__with-arrow .arrow-wrapper .icon-svg {
  position: absolute;
  top: 0;
  left: -4.25rem;
  fill: #312F2E;
  display: inline-block;
  height: 0.75rem;
  width: 2.125rem;
}
.btn__with-arrow .arrow-wrapper .icon-svg:first-of-type {
  left: 0;
}
@media screen and (min-width: 1200px) {
  .btn__with-arrow:hover {
    color: #F4BC49;
  }
  .btn__with-arrow:hover .arrow-wrapper .icon-svg {
    left: 0;
    transition: all 0.5s cubic-bezier(0.22, 0.44, 0, 1);
  }
  .btn__with-arrow:hover .arrow-wrapper .icon-svg:first-of-type {
    left: 4.25rem;
  }
}
.btn__hamburger {
  background-color: #F4BC49;
  padding: unset;
  border-radius: 0.875rem;
  height: 3.125rem;
  width: 3.125rem;
}
@media screen and (max-width: 539px) {
  .btn__hamburger {
    border-radius: 0.625rem;
    height: 2.5rem;
    width: 2.5rem;
  }
}
.btn__hamburger > span {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: #F4BC49;
  transform: translate(-50%, -50%);
  height: 0.125rem;
  width: 1.5rem;
}
@media screen and (max-width: 539px) {
  .btn__hamburger > span {
    width: 1.25rem;
  }
}
.btn__hamburger > span::before, .btn__hamburger > span::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-color: inherit;
  height: inherit;
  width: inherit;
}
.btn__hamburger > span::before {
  transform: translateY(-0.4375rem);
}
@media screen and (max-width: 539px) {
  .btn__hamburger > span::before {
    transform: translateY(-0.375rem);
  }
}
.btn__hamburger > span::after {
  transform: translateY(0.4375rem);
}
@media screen and (max-width: 539px) {
  .btn__hamburger > span::after {
    transform: translateY(0.375rem);
  }
}
.btn__close {
  background-color: #F4BC49;
  padding: unset;
  border-radius: 0.875rem;
  height: 3.125rem;
  width: 3.125rem;
}
@media screen and (max-width: 539px) {
  .btn__close {
    border-radius: 0.625rem;
    height: 2.5rem;
    width: 2.5rem;
  }
}
.btn__close > span {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: transparent;
  transform: translate(-50%, -50%);
  height: 0.125rem;
  width: 1.5rem;
}
@media screen and (max-width: 539px) {
  .btn__close > span {
    width: 1.25rem;
  }
}
.btn__close > span::before, .btn__close > span::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background-color: #FFFFFF;
  height: inherit;
  width: inherit;
}
.btn__close > span::before {
  transform: rotate(-45deg);
}
.btn__close > span::after {
  transform: rotate(45deg);
}

.breadcrumbs {
  margin-bottom: 40px;
}
.breadcrumbs__inner {
  overflow: hidden;
  width: 100%;
}
@media screen and (max-width: 539px) {
  .breadcrumbs__inner {
    white-space: nowrap;
    overflow: scroll;
  }
}
.breadcrumbs__list li {
  display: inline-block;
  position: relative;
  font-weight: bold;
  font-size: 1rem;
  line-height: 1.25rem;
}
@media screen and (max-width: 539px) {
  .breadcrumbs__list li {
    font-size: 0.875rem;
    line-height: 1.125rem;
  }
}
.breadcrumbs__list li + li {
  padding-left: 1.5625rem;
}
.breadcrumbs__list li + li::before {
  position: absolute;
  left: 0;
  content: "/";
  left: 4px;
  color: #312F2E;
  margin: 0 0 0 0.4375rem;
}
.breadcrumbs__list li:last-child span {
  padding-left: 4px;
}
.breadcrumbs__list li:last-child a,
.breadcrumbs__list li:last-child span {
  color: #FFFFFF;
}
.breadcrumbs__list a {
  color: #F4BC49;
}

.pagination__list {
  font-weight: 700;
}
.pagination__list li {
  display: inline-block;
}
.pagination__list li + li {
  margin-left: 6px;
}
.pagination__list a,
.pagination__list span {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  color: #312F2E;
}
.pagination__list a.active,
.pagination__list span.active {
  color: #FFFFFF;
}
.pagination__list a .pagination__btn-next,
.pagination__list a .pagination__btn-prev {
  width: 30px;
}
.pagination__list img {
  width: 100%;
}

.no-scroll {
  position: relative;
  overflow-y: hidden;
}

.scroll-on {
  visibility: visible;
  overflow-y: auto;
  opacity: 1;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
  height: 100%;
}

body {
  background-color: #E5DDD4;
  color: #312F2E;
  font-family: "DIN Pro", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: normal;
  height: 100%;
  width: 100%;
  overflow-x: hidden !important;
  -webkit-font-smoothing: antialiased;
}
body::-webkit-scrollbar {
  background-color: #312F2E;
  width: 0.375rem;
}
body::-webkit-scrollbar-thumb {
  background-color: #F4BC49;
  border-radius: 0.125rem;
}

ul {
  font-size: pxTorem(16px);
  line-height: pxTorem(24px);
}
@media screen and (max-width: 991px) {
  ul {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
}
@media screen and (max-width: 539px) {
  ul {
    font-size: 0.75rem;
    line-height: 0.875rem;
  }
}

.container {
  margin: 0 auto;
  max-width: 80rem;
  width: 100%;
}
@media screen and (max-width: 991px) {
  .container {
    width: calc(100% - 9.375rem);
  }
}
@media screen and (max-width: 767px) {
  .container {
    width: calc(100% - 5rem);
  }
}
@media screen and (max-width: 599px) {
  .container {
    width: calc(100% - 1.875rem);
  }
}

.header {
  position: fixed;
  top: 0;
  padding: 15px;
  color: #fff;
  width: 100%;
  height: 60px;
  transition: all 0.3s ease-in-out;
  z-index: 99;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(114deg, #2B251F 15.18%, #484236 96.04%);
  height: 100%;
  width: 100%;
  z-index: -1;
  transition: all 0.3s ease-in-out;
}

.scroll-up {
  transform: translateY(0);
}

.scroll-down {
  transform: translateY(-100%);
}

body.homepage .header::before {
  opacity: 0;
}
body.homepage.scrolled .header::before {
  opacity: 1;
}

.navigation-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-btn {
  display: none;
  background: none;
  border: none;
  color: #fff;
  font-size: 1.5rem;
  cursor: pointer;
}

.navigation ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.navigation ul li {
  margin-right: 20px;
}
.navigation ul li:last-child {
  margin-right: 0;
}
.navigation ul li a {
  text-decoration: none;
  color: #fff;
  transition: color 0.3s ease;
}
.navigation ul li a:hover {
  color: #ffd700;
}
.navigation ul li.dropdown {
  position: relative;
}
.navigation ul li.dropdown .dropbtn {
  cursor: pointer;
}
.navigation ul li.dropdown .dropdown-content {
  display: none;
  position: absolute;
  background-color: #333;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.navigation ul li.dropdown .dropdown-content a {
  color: #fff;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  transition: background-color 0.3s ease;
}
.navigation ul li.dropdown .dropdown-content a:hover {
  background-color: #555;
}
.navigation ul li.dropdown:hover .dropdown-content {
  display: block;
}

@media screen and (max-width: 768px) {
  .menu-btn {
    display: block;
  }
  .navigation {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }
  .navigation ul li {
    margin: 10px 0;
  }
  .navigation.active {
    display: flex;
  }
}
.section__hero {
  position: relative;
  background: linear-gradient(114deg, #2B251F 15.18%, #484236 96.04%);
  width: 100vw;
}
.section__hero::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: -0.1875rem;
  background-image: url(../images/hero-image.png);
  background-size: contain;
  background-repeat: no-repeat;
  transform: translateX(21%);
  height: 100%;
  width: 100%;
}
@media screen and (min-width: 1200px) {
  .section__hero {
    height: 100vh;
    min-height: 810px;
  }
}
@media screen and (max-width: 1199px) {
  .section__hero {
    height: unset;
  }
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
  touch-action: none;
  z-index: 100;
  transition: all 0.15s ease-in-out;
}
.modal.opened {
  opacity: 1;
  visibility: visible;
  touch-action: auto;
}
.modal .modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}
.modal .modal-content form {
  display: flex;
  flex-direction: column;
}
.modal .modal-content form label {
  margin-bottom: 5px;
}
.modal .modal-content form input {
  margin-bottom: 15px;
  padding: 8px;
}
.modal .modal-content form button {
  background-color: #ffd700;
  color: #333;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}
